import Vue from 'vue';
import Component from 'vue-class-component';

@Component
export class TagHelper extends Vue {

    createTag() {
        const name = window.prompt('请输入标签名');
        if (name) {
            if (name.trim()===''){
                window.alert('全是空格不行哦')
            }else {
            this.$store.commit('createTag', name);
            }
        }else {
            window.alert('请输入内容')
        }
    }
}

export default TagHelper;