






import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class QRcode extends Vue {

  show = true;

  mounted() {
    document.body.addEventListener('click', () => {
      this.show = false;
    }, false);

    if (window.outerWidth<=700){
      this.show=false
    }
  }
  }



